.profile__info {
  margin: 0 30px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media screen and (max-width: 620px) {
  .profile__info {
    justify-content: center;
    align-content: center;
    margin-bottom: 30px;
    flex-grow: 0;
  }
}
